import {useEffect} from "react"
import {getAllLeagues} from "../api/api"
import {SoccerStatContext} from "../context/context"
import {useContext, useState} from "react"
import {Loader} from "../components/Loader"
import {Link, useHistory, useLocation} from "react-router-dom"
import {Card, List, Image, message} from "antd"
import {SetSearch} from "../components/SetSearch"

const {Meta} = Card

function League() {
  const [filteredLeague, setFilteredLeague] = useState([])
  const {leagues, loading, setLeagues, resetLoading} =
    useContext(SoccerStatContext)
  const {push} = useHistory()
  const {pathname, search} = useLocation()

  const handleSearch = str => {
    setFilteredLeague(
      leagues.filter(item =>
        item.name.toLowerCase().includes(str.toLowerCase())
      )
    )
    push({
      pathname,
      search: `search=${str}`
    })
  }
  useEffect(() => {
    localStorage.getItem("token")
  })
  useEffect(() => {
    getAllLeagues()
      .then(data => {
        setLeagues(data.competitions)
        setFilteredLeague(
          search
            ? data.competitions.filter(item =>
                item.name
                  .toLowerCase()
                  .includes(search.split("=")[1].toLowerCase())
              )
            : data.competitions
        )
      })
      .catch(error => {
        message.error("ОШИБКА СЕРВЕРА, ПОПРОБУЙТЕ ЧУТЬ ПОЗЖЕ!")
      })

    return resetLoading()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search])

  return (
    <>
      <SetSearch cb={handleSearch} />
      {loading ? (
        <Loader />
      ) : (
        <List
          grid={{
            gutter: 16,
            xs: 1,
            sm: 2,
            md: 4,
            lg: 4,
            xl: 5,
            xxl: 4
          }}
          dataSource={filteredLeague}
          renderItem={item => (
            <List.Item>
              <Link to={`/leagues/${item.id}/overview/teams`}>
                <Card
                  hoverable
                  style={{width: 200}}
                  cover={
                    <Image
                      width={200}
                      height={200}
                      alt={item.name}
                      preview={false}
                      src={
                        item.emblemUrl
                          ? item.emblemUrl
                          : item.area.ensignUrl
                          ? item.area.ensignUrl
                          : "error"
                      }
                      fallback="data:image/png;base64,"
                    />
                  }
                >
                  <Meta title={item.name} description={item.area.name} />
                </Card>
              </Link>
            </List.Item>
          )}
        />
      )}
    </>
  )
}

export {League}
